.current {
    color:red;
}
h1{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    text-decoration: underline;
}

body{
    background-color: lightgreen;
}
nav{
    border: 2px solid black;
    outline: 10px solid green;
    font-size: 200%;

}
.haegri{
    border: 3px solid black;
    outline: green solid 10px;
    font-size: 20px;
    padding-bottom: 200%;
    padding-left: 20px;     
    
}
.vinstri{
    font-size: 25px;
}
a:hover{
    color: darkblue;
}
*{
    box-sizing: border-box;
}
.box {
    float: left;
    width: 50%;
    padding-left: 50px;
}
  
.clearfix::after {
    content: "";
    clear: both;
    display: table;
}
img{
    width: 50%
}